import { EventBus } from "./event-bus";
import { i18n } from "./plugins/i18n";

// Call modal example:
// Utils.confirmModal("Consent required", "Are you sure?", (confirm) => {
//   if (confirm){
//    console.log(confirm);
//   }
// });

const utils = {
  alertModal(title, message, variant) {
    let data = Object.assign(
      {},
      { title: title, message: message, variant: variant, type: "alert" }
    );

    EventBus.$emit("show", data);
  },
  confirmModal(title, message, variant, callback) {
    let data = Object.assign(
      {},
      {
        title: title,
        message: message,
        variant: variant,
        type: "confirm",
        callback: callback
      }
    );

    EventBus.$emit("show", data);
  },
  movieModal(movie) {
    let data = Object.assign({}, { movie: movie });

    EventBus.$emit("show-movie", data);
  },
  showError(err) {
    if (err.response && err.response.data) {
      let variant = "";
      let result = "";

      for (let error in err.response.data) {
        result += err.response.data[error];
        variant = error;
      }

      if (variant && result) {
        return this.alertModal(i18n.t(variant), result, variant);
      }
    }

    if (err) {
      return this.alertModal(i18n.t("Error"), err.message, "danger");
    }
  }
};

export default utils;



